import React, { useState } from "react";
import "./Allbooks.css";
import booksData from "../Components/books.json"; // Import your JSON data
function Allbooks(){
  const [searchTerm, setSearchTerm] = useState(""); // Step 2: State for search term

  // Step 3: Event handler to update the search term
  const handleSearch = (event) => {
    setSearchTerm(event.target.value);
  };

  // Step 4: Filter books based on the search term
  const filteredBooks = booksData.filter((book) =>
    book.title.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="books-page">
      <input
        className="search-input"
        type="text"
        placeholder="Search a Book"
        value={searchTerm} // Bind input value to search term state
        onChange={handleSearch} // Step 3: Add onChange event handler
      />
      <div className="books">
      {filteredBooks.map((book) => (
  <div className="book-card" key={book.id}>
      <img src={`https://picsum.photos/id/${book.id}/367/267`} alt="error" />
    <p className="bookcard-title">{book.title}</p>
    <p className="bookcard-author">By {book.author}</p>
    <div className="bookcard-category">
      <p>{book.category}</p>
    </div>
    <div className="bookcard-emptybox"></div>
  </div>
))}
      </div>
    </div>
  );
}

export default Allbooks;
